import React, {Component} from 'react';
import logo from './logo.svg';
import './App.css';
import $ from 'jquery'

class App extends Component {
    constructor(props) {
        super(props)
    }

    componentDidMount() {
        $(document).ready(function () {
            $('.title').click(function () {
                $('.container').addClass('open');
            });


            $('.close').click(function () {
                $('.container').removeClass('open');
            });
        });
    }

    render() {
        return (
            <div className="App">
                <div className="bgoverlay">
                    <div className="container">
                        <span className="ico">
                        <span className="ico2"/>
                        <span className="title">Click Me</span>
                        </span>
                        <div className="endtext">
                            <span className="close" title="Restart"><i className="fa fa-times"/></span>
                            <h1>Dear Baozi</h1>
                            <h1>You make me want to be a better man</h1>
                            <h1>May this Valentine bless us with the cupid of love and warmth of romance. Happy
                                Valentine’s Day Honey!</h1>
                            <h1>I love you</h1>
                            <h3>~Kurt Tsai</h3>
                            <h3>2018/03/14</h3>
                        </div>
                    </div>
                </div>

            </div>
        );
    }
}

export default App;



